import { supabase } from '../lib/supabase';

interface PushPayload {
  title: string;
  body: string;
  data?: Record<string, string>;
}

/**
 * Supabase Edge Function    
 */
export const sendPushToUser = async (userId: string, payload: PushPayload) => {
  try {
    const { data, error } = await supabase.functions.invoke('send-push', {
      body: {
        userId,
        title: payload.title,
        body: payload.body,
        data: payload.data || {}
      }
    });

    if (error) {
      console.error('  :', error);
      return { success: false, error };
    }

    console.log('   :', data);
    return { success: true, result: data };
  } catch (error) {
    console.error('  :', error);
    return { success: false, error };
  }
};

/**
 *    
 */
export const sendMatchRequestPush = async (
  toUserId: string,
  fromUserName: string,
  fromUserId: string
) => {
  return sendPushToUser(toUserId, {
    title: '   ',
    body: `${fromUserName}  `,
    data: {
      type: 'match_request',
      from_user_id: fromUserId
    }
  });
};

/**
 *    
 */
export const sendMatchSuccessPush = async (
  toUserId: string,
  matchedUserName: string,
  chatRoomId?: string
) => {
  return sendPushToUser(toUserId, {
    title: '  !',
    body: `${matchedUserName} !  .`,
    data: {
      type: 'match_success',
      chat_room_id: chatRoomId || ''
    }
  });
};

/**
 *    
 */
export const sendMessagePush = async (
  toUserId: string,
  fromUserName: string,
  messagePreview: string,
  chatRoomId: string
) => {
  return sendPushToUser(toUserId, {
    title: ` ${fromUserName}`,
    body: messagePreview.length > 50 ? messagePreview.slice(0, 50) + '...' : messagePreview,
    data: {
      type: 'message',
      chat_room_id: chatRoomId
    }
  });
};
