import { supabase } from '../lib/supabase';

/**
 * 24  pending     
 *      
 */
export const cleanupExpiredMatchingRequests = async () => {
  try {
    const { error } = await supabase.rpc('auto_delete_expired_matching_requests');
    
    if (error) {
      console.error('    :', error);
      return false;
    }
    
    console.log('     ');
    return true;
  } catch (error) {
    console.error('    :', error);
    return false;
  }
};

/**
 *   pending   
 */
export const getPendingMatchingRequests = async (userId: string) => {
  try {
    const { data, error } = await supabase
      .from('matching_requests')
      .select('*')
      .or(`from_user_id.eq.${userId},to_user_id.eq.${userId}`)
      .eq('status', 'pending')
      .order('created_at', { ascending: false });

    if (error) throw error;
    return data || [];
  } catch (error) {
    console.error('   :', error);
    return [];
  }
};

/**
 *   
 */
export const acceptMatchingRequest = async (requestId: number) => {
  try {
    const { error } = await supabase
      .from('matching_requests')
      .update({ status: 'accepted' })
      .eq('id', requestId);

    if (error) throw error;
    return true;
  } catch (error) {
    console.error('  :', error);
    return false;
  }
};

/**
 *   
 */
export const rejectMatchingRequest = async (requestId: number) => {
  try {
    const { error } = await supabase
      .from('matching_requests')
      .update({ status: 'rejected' })
      .eq('id', requestId);

    if (error) throw error;
    return true;
  } catch (error) {
    console.error('  :', error);
    return false;
  }
};
